import apiClient from '../api/client';

const userService = {
  // Rcuprer les transporteurs avec filtres optionnels
  getTransporteurs: async (filters = {}) => {
    try {
      const response = await apiClient.get('/users/transporteurs', { params: filters });
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la rcupration des transporteurs' };
    }
  },

  // Rcuprer le profil d'un utilisateur
  getUserProfile: async (userId) => {
    try {
      const response = await apiClient.get(`/users/profile/${userId}`);
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la rcupration du profil utilisateur' };
    }
  },

  // Rcuprer son propre profil
  getMyProfile: async () => {
    try {
      const response = await apiClient.get('/users/profile');
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la rcupration de votre profil' };
    }
  },

  // Mettre  jour son profil
  updateProfile: async (userData) => {
    try {
      const response = await apiClient.put('/users/profile', userData);
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la mise  jour du profil' };
    }
  },

  // Tlcharger une photo de profil
  uploadProfilePhoto: async (formData) => {
    try {
      const response = await apiClient.post('/users/profile/upload-photo', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors du tlchargement de la photo' };
    }
  },

  // Tlcharger des documents (pour les transporteurs)
  uploadDocuments: async (formData) => {
    try {
      const response = await apiClient.post('/users/profile/upload-documents', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors du tlchargement des documents' };
    }
  },

  // Mettre  jour le mot de passe
  updatePassword: async (passwordData) => {
    try {
      const response = await apiClient.put('/users/profile/update-password', passwordData);
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la mise  jour du mot de passe' };
    }
  }
};

export default userService;