import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';

/**
 * Composant ProtectedRoute pour protger les routes ncessitant une authentification
 * @param {boolean} isAuthenticated - Utilisateur authentifi ou non
 * @param {Array} roles - Rles autoriss pour accder  la route
 * @param {string} userRole - Rle de l'utilisateur
 * @param {ReactNode} children - Contenu de la route
 */
const ProtectedRoute = ({ 
  isAuthenticated, 
  roles = [], 
  userRole,
  children 
}) => {
  const location = useLocation();
  
  // Si l'utilisateur n'est pas authentifi, rediriger vers la page de connexion
  if (!isAuthenticated) {
    return <Navigate to="/login" state={{ from: location.pathname }} replace />;
  }
  
  // Si des rles sont spcifis et que l'utilisateur n'a pas le rle requis
  if (roles.length > 0 && !roles.includes(userRole)) {
    // Rediriger vers le tableau de bord avec un message d'accs refus
    return <Navigate to="/dashboard" state={{ 
      accessDenied: true, 
      message: "Vous n'avez pas les permissions ncessaires pour accder  cette page." 
    }} replace />;
  }
  
  // Si l'utilisateur est authentifi et a les permissions ncessaires
  return children;
};

export default ProtectedRoute;