import apiClient from '../api/client';

const avisService = {
  // Crer un nouvel avis
  createAvis: async (avisData) => {
    try {
      const response = await apiClient.post('/avis', avisData);
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la cration de l\'avis' };
    }
  },

  // Rcuprer les avis d'un utilisateur spcifique
  getAvisUtilisateur: async (userId, page = 1, limit = 10) => {
    try {
      const response = await apiClient.get(`/avis/utilisateur/${userId}`, {
        params: { page, limit }
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la rcupration des avis' };
    }
  },

  // Rcuprer les avis donns par l'utilisateur connect
  getAvisDonnes: async (page = 1, limit = 10) => {
    try {
      const response = await apiClient.get('/avis/donnes', {
        params: { page, limit }
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la rcupration des avis donns' };
    }
  },

  // Rcuprer les avis reus par l'utilisateur connect
  getAvisRecus: async (page = 1, limit = 10) => {
    try {
      const response = await apiClient.get('/avis/recus', {
        params: { page, limit }
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || { message: 'Erreur lors de la rcupration des avis reus' };
    }
  }
};

export default avisService;